import { required } from 'vuelidate/lib/validators'
import { hashPassword } from '@/shared/Hash'
export default {
    name: 'Login',
    data() {
        return {
            form: {
                username: '',
                password: ''
            },
            error: '',
            state: {
                isSessionNormal: true
            }
        }
    },
    validations: {
        form: {
            username: { required },
            password: { required }
        }
    },
    methods: {
        handleLogin() {
            this.$v.$touch()
            if (this.$v.$invalid) {
                this.state.isError = true;
                this.error = 'Username and password should not be empty.'
            } else {
                let username = this.form.username
                let password = hashPassword(username, this.form.password).toString()
                this.$store.dispatch('login', { username, password })
                .then(() => {
                    this.$router.push('/admin')
                })
                .catch((error) => {
                    this.error = error.response.data.info;
                })
            }
        },
        handleLogout() {
            this.$store.dispatch('logout');
            this.$router.push('/login');
        },
        onCloseAlertError() {
            this.error = ''
        }
    }
}